import rollStats from './dice.js'

class AppState  {
    constructor() {
        this.rolls = {
            'str': 10,
            'dex': 11,
            'con': 12,
            'int': 13,
            'wis': 14,
            'cha': 15
        };
        this.hasChanged = false;
    }

    getRolls(){
        return this.rolls;
    }
    getRoll(key){
        return this.rolls[key];
    }
    setRoll(key, val){
        this.rolls[key] = val;
        this.hasChanged = true;
    }

    roll(gen){
        let keys = Object.keys(this.rolls);
        let rolls = rollStats(gen);
        for (let i=0; i<rolls.length; i++){
            this.rolls[keys[i]] = rolls[i].sum;
        }
    }


}

export default AppState;
