import React, { Component } from 'react'
import Ability from './Ability.js'

class CharacterPanel extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rolls: this.props.appState.rolls
        }
        this.genMethod = '4d6 Drop Lowest';
        this.drop = this.drop.bind(this);
        this.rollStats = this.rollStats.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleMouseEnter = this.handleMouseEnter.bind(this);
        this.handleMouseLeave = this.handleMouseLeave.bind(this);
    }

    drop(e) {
        e.preventDefault();
        let src = document.getElementById(e.dataTransfer.getData("src"));
        let tgt = e.target.parentElement;

        let srcId = src.id;
        let tgtId = tgt.id;

        let tgtVal = this.props.appState.rolls[tgtId];
        let srcVal = this.props.appState.rolls[srcId];
        this.props.appState.setRoll(tgtId, srcVal);
        this.props.appState.setRoll(srcId, tgtVal);

        this.setState({rolls: this.props.appState.rolls});
    }

    rollStats(e) {
        this.props.appState.roll(this.genMethod);
        this.setState(
            {'rolls': this.props.appState.rolls}
        );
    }

    handleMouseEnter(e) {
        let element = document.getElementById("gradient");
        element.style.top = '30px';
    }

    handleMouseLeave(e) {
        let element = document.getElementById("gradient");
        element.style.top = '';
    }

    handleClick(e) {
        document.getElementById("active").id = ""
        this.genMethod = e.target.firstChild.textContent;
        e.target.id = "active";
    }

    handleTouchMove(e) {
        console.log('moving')
        e.preventDefault();
    }

    render() {
        let abilities = [];
        
        let rolls = this.props.appState.rolls

        for (let key in rolls){
            abilities.push(
                <Ability id={key} key={key} className="ability" draggable="true" 
                    roll={rolls[key]} appState={this.props.appState}/>
            )
        }
        return (
            <div 
                className={this.props.className} 
                id={this.props.id}
                onDrop={this.drop}
                onTouchCancel={this.drop}
                onMouseEnter={this.handleMouseEnter}
                onMouseLeave={this.handleMouseLeave}>
                    {abilities}
                
                <div className="button-div">
                    <button className="roll-button" onClick={this.rollStats}>
                        Roll
                    </button>
                </div>
                <div className="button-div select">
                    <button id="active" onClick={this.handleClick}>4d6 Drop Lowest</button>
                    <button onClick={this.handleClick}>3d6</button>
                    <button onClick={this.handleClick}>Critical Role</button>
                    <button onClick={this.handleClick}>1d20</button>
                </div>
                <div className="gradient" id="gradient"></div>
            </div>
        )
    }
}

export default CharacterPanel
